"use server";

import { EnumResponse } from "@/types/base";
import { User } from "@/types/user";
import { UserProfileForm } from "../validation/user";
import { ApiError, destroy, GenericResponse, get, post, put } from "./baseApi";
import {
  USER_EMPLOYMENT_STATUS_ENDPOINT,
  USER_ENDPOINT,
  USER_PROFILE_ENDPOINT,
  USER_PROFILE_PICTURE_ENDPOINT,
  USER_RELIGION_ENDPOINT,
} from "./endpoints";

export const getUsers = async (): Promise<GenericResponse<User[]>> => {
  try {
    return await get(USER_ENDPOINT);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: [] as User[],
      message: error.message,
    };
  }
};

export const getUserByEmployeeId = async (
  employeeId: string
): Promise<GenericResponse<User>> => {
  try {
    return await get(USER_PROFILE_ENDPOINT + `/${employeeId}`);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: {} as User,
      message: error.message,
    };
  }
};

export const getProfilePicture = async ({
  employeeId,
}: {
  employeeId: string;
}): Promise<Blob> => {
  try {
    return await get(`${USER_PROFILE_PICTURE_ENDPOINT}/${employeeId}`, {
      "Content-Type": "blob",
    });
  } catch (err) {
    console.log(err);
    return new Blob();
  }
};

export const getUserReligions = async (): Promise<
  GenericResponse<EnumResponse[]>
> => {
  try {
    return await get(`${USER_RELIGION_ENDPOINT}`);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: [] as EnumResponse[],
      message: error.message,
    };
  }
};

export const getUserEmploymentStatus = async (): Promise<
  GenericResponse<EnumResponse[]>
> => {
  try {
    return await get(`${USER_EMPLOYMENT_STATUS_ENDPOINT}`);
  } catch (err) {
    console.log(err);
    const error = err as ApiError;
    return {
      success: false,
      data: [] as EnumResponse[],
      message: error.message,
    };
  }
};

export const createUser = async (
  user: UserProfileForm
): Promise<GenericResponse<User>> => {
  try {
    return await post(USER_ENDPOINT, user);
  } catch (err) {
    const error = err as ApiError;
    return { success: false, data: {} as User, message: error.message };
  }
};

export const updateUser = async (
  user: UserProfileForm,
  userId: string
): Promise<GenericResponse<User>> => {
  try {
    return await put(USER_ENDPOINT, user, userId);
  } catch (err) {
    const error = err as ApiError;
    return { success: false, data: {} as User, message: error.message };
  }
};

export const deleteUser = async (
  userId: string
): Promise<GenericResponse<string>> => {
  try {
    return await destroy(USER_ENDPOINT, userId);
  } catch (err) {
    const error = err as ApiError;
    return { success: false, data: "", message: error.message };
  }
};

export const uploadUserProfilePicture = async ({
  employeeId,
  body,
}: {
  employeeId: string;
  body: FormData;
}): Promise<GenericResponse<string>> => {
  try {
    return await post(
      `${USER_PROFILE_PICTURE_ENDPOINT}/${employeeId}`,
      body,
      "multipart/form-data"
    );
  } catch (err) {
    const error = err as ApiError;
    return { success: false, data: "", message: error.message };
  }
};
